___CSS_LOADER_EXPORT___.push([module.id, `.toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
}
#view-title {
  font-size: 28px;
  margin: 0;
}
.toolbar-right {
  gap: 16px;
}

.btn {
  border: 1px solid var(--line);
  font-weight: 700;
  font-size: 16px;
  background: #fff;
  cursor: pointer;
  height: 46px;
  padding: 0 20px;
}

/* ▼ E-STOP 인디케이터+토글 (각진) */
.btn.estop {
  min-width: 120px;
}

/* READY: 흰배경 + 레드 테두리 → ‘누르면 정지’ */
.btn.estop[data-state='ready'] {
  background: #fff;
  color: var(--brand-red);
  border-color: var(--brand-red);
}

/* WARNING: 옅은 앰버 배경 */
.btn.estop[data-state='warning'] {
  background: #fff7ed;
  color: var(--state-amber-900);
  border-color: var(--state-amber);
}

/* EMERGENCY: 강한 레드 채움(정지 루틴 진행 중) */
.btn.estop[data-state='emergency'] {
  background: var(--brand-red);
  color: #fff;
  border-color: var(--brand-red);
}

/* E-STOPPED(래치): 더 어두운 레드 계열, 버튼 라벨은 RESET으로 바뀜 */
.btn.estop[data-state='latched'] {
  background: var(--state-red-dark);
  color: #fff;
  border-color: var(--state-red-dark);
}

.btn.ghost {
  background: #fff;
  color: var(--ink-700);
}
