  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
body {
  margin: 0;
  font-family: 'Noto Sans KR', 'Malgun Gothic', 'Segoe UI', system-ui,
    -apple-system, sans-serif;
  font-size: 16px;
  color: var(--ink-900);
  background: var(--bg);
}
a {
  color: inherit;
  text-decoration: none;
}

.caption {
  font-size: 12px;
  color: var(--ink-500);
  letter-spacing: 0.2px;
}
.muted {
  color: var(--ink-500);
}
.mono {
  font-family: Consolas, 'Courier New', ui-monospace, monospace;
  letter-spacing: 0.2px;
}
